.FriendList{
    display: flex;
justify-content: center;
    border: 1px solid brown;
    padding: 10px 10px;
    margin-right: auto;
    margin-left: auto;
    max-width: 500px;
    
}

.FriendListItem{
   text-align: center;
    padding: 10px;
width: auto;
margin: 10px;
border: 1px solid rgb(241, 241, 119);
box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
  0px 1px 3px rgba(222, 247, 0, 0.12);
border-radius: 0px 0px 4px 4px;
}

.online {
    composes: FriendListItem;
    background-color: green;
}

.offline{
    composes: FriendListItem;
    background-color: red;
}

.name{
    margin-top: 5px;
}
