.statistics{
    padding: 10px;
width: 400px;
margin-top: 0;
margin-right: auto;
margin-left: auto;
margin-bottom: 20px;
text-align: center;
background: white;
border: 1px solid red;
box-shadow: 0px 2px 1px rgba(255, 8, 8, 0.589), 0px 1px 3px rgba(255, 8, 8, 0.733),
  0px 1px 3px rgba(230, 8, 8, 0.589);
border-radius: 4px 4px 4px 4px;
}

.statList{
display: flex;
padding: 0;
margin: 0;
line-height: 1.5;
align-items: center;
}

.item{
padding: 10px;
background-color: rgba(255, 255, 0, 0.658);
color: rgb(10, 5, 5);
}

.label{
line-height: 1.5;
}

.percentage{
    font-size: 25px;
}