.profile{
    padding: 10px;
    width: 300px;
    margin: 20px auto;
    text-align: center;
    background: white;
    border: 1px solid green;
    box-shadow: 1px 2px 1px rgba(4, 70, 4, 0.685), 1px 1px 1px rgba(4, 70, 4, 0.685),
  1px 1px 3px rgba(4, 70, 4, 0.685);
border-radius: 4px 4px 4px 4px;
}

.avatar{
  margin: 0 auto;
  width: 100px;
  border-radius: 50%;
}

.name{
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: 30px;
  font-weight: 700;
}

.tag {
  margin-top: 0;
  margin-bottom: 15px;
  color: cadetblue;
}

.location {
  margin-top: 0;
  margin-bottom: 15px;
  color: cadetblue;
}


.description{
  margin-top: 0;
  text-align: center;
}

.stats{
  display: flex;
  padding: 20px 20px;
  margin: 0;
  line-height: 1.5;
  align-items: center;
  border-top: 1px solid cadetblue;
}

.lable{
  color: cadetblue;
}

.quantity{
  font-weight: 700;
}